import { useForm, SubmitHandler } from 'react-hook-form';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import SubmitButton from '../Components/SubmitButton';
import * as Api from '../lib/Api';
import {IAuthForm} from '../interfaces/IAuthForm';

const Container = styled.div`
    flex-direction: column;
    margin: 5rem auto;
`;
const Form = styled.form`
    height: 100%;
    width: 100%;
    margin: 3rem auto;
`;
const Grid = styled.div`
    display: grid;
    grid-template-columns: 0.2fr 0.8fr;
    grid-gap: 0.5rem;
    justify-content : left;
    text-align: right;
    width: 50%;
    margin: 2rem auto;
    padding: 1rem 0rem 1rem 7rem;
`;
const RadioGroup = styled.div`
    display: felx;
    flex-direction: row;
    padding: 0 1rem;
    align-items: center;
    margin: 0.3rem;
`;
const RadioButton = styled.div`
    &:not(first-child) {
        margin-right : 6rem;
    }
    accent-color: red;
`;
const InputDiv = styled.div`
    display: flex;
    flex-direction: column;
    width: 60%;
    padding: 0 1rem;
    margin: 0.3rem;
`;
const Input = styled.input`
    width: 95%;
    height: 2.3rem;
    padding: 3px 12px;
    border: none;
    border-radius: 5px;
    background-color: #ffe8cc;
`;
const Label = styled.label`
    margin: 0.8rem;
`;


const SignUp = () => {
    const navigate = useNavigate();
    const {     
        register,
        formState: { errors },
        handleSubmit,
    } = useForm<IAuthForm>({mode : 'onBlur'});
    
    const onSubmit: SubmitHandler<IAuthForm> = data => join(data);
    
    const getValue = (id : string) : (string | null) =>{
        const pw = (document.querySelector(`#${id}`) as HTMLInputElement | null)?.value;
        return pw || '';
    }
    
    const join = async ({ name, schoolNumber, email, password, userType}:IAuthForm) => {
		try {
			const joinData = { name, schoolNumber, email, password,userType };
			await Api.post(`/signup`, joinData).then((res) => {
                let {schoolNumber, name} = res.data.result;
				alert(` ${schoolNumber}(${name})  .`);
				navigate('/');
			});
		} catch (e) {
			alert(e);
		}
	};

    return (
        <Container>
            <h1></h1>
            <Form onSubmit={handleSubmit(onSubmit)}>
                <Grid>
                    <Label htmlFor='name'></Label>
                    <InputDiv>
                    <Input 
                        id = 'name'
                        type = 'text'
                        placeholder=' .'
                        {...register('name', {    
                                required: ' .',
                                minLength: { 
                                    value: 2,
                                    message: '2  .'
                                },
                                pattern: { 
                                    // memo :  
                                    value: /^[A-za-z-]{2,6}$/,
                                    // memo:  
                                    message: ' :  ,   ', 
                                },
                            }
                        )}
                    />
                    {errors.name && <small role='alert'>{errors.name.message}</small>}
                    </InputDiv>
                    
                    <Label htmlFor='schoolNumber'></Label>
                    <InputDiv>
                        <Input 
                            id = 'schoolNumber'
                            type = 'text'
                            placeholder=' .'
                            {...register('schoolNumber', {
                                required: ' .',
                                pattern: {
                                    value: /[0-9]{10}/,
                                    message: ' (,10)  .',
                                },
                            })} 
                        />                
                        {errors.schoolNumber && <small role='alert'>{errors.schoolNumber.message}</small>}
                    </InputDiv>
                    
                    <Label htmlFor='email'>e-mail</Label>
                    <InputDiv>
                        <Input
                            id='email'
                            type='text'
                            placeholder='dongguk@email.com'
                            {...register('email', {
                                required: ' .',
                                pattern: {
                                    value: /\S+@\S+\.\S+/,
                                    message: '   .',
                                },
                            })}
                        />
                        {errors.email && <small role='alert'>{errors.email.message}</small>}
                    </InputDiv>

                    <Label></Label>                
                    <RadioGroup>
                        <RadioButton>
                            <input type='radio' id='student' value='STUDENT' defaultChecked {...register('userType')}/> 
                            <label htmlFor='student'></label>
                        </RadioButton>

                        <RadioButton>
                            <input type='radio' id='prof' value='PROFESSOR' {...register('userType')}/> 
                            <label htmlFor='prof'></label>
                        </RadioButton>
                        
                        <RadioButton>
                            <input type='radio' id='prof' value='PROFESSOR' {...register('userType')}/> 
                            <label htmlFor='prof'></label>
                        </RadioButton>
                    </RadioGroup>
                    
                    <Label htmlFor='password'></Label>
                    <InputDiv>
                        <Input
                            id='password'
                            type='password'
                            placeholder='********'
                            {...register('password', {
                                required: '  .',
                                minLength: {
                                    value: 8,
                                    message: '8   .',
                                },
                                pattern : {
                                    value : /(?=.*[0-9])(?=.*[a-z])(?=.*\W)(?=\S+$).{8,20}/,
                                    message : ', ,   8~20'
                                }
                            })}
                        />
                        {errors.password && <small role='alert'>{errors.password.message}</small>}
                    </InputDiv>
                    <Label htmlFor='password'> </Label>
                    <InputDiv>
                        <Input
                            type='password'
                            placeholder='********'
                            {...register('passwordConfirm', {
                                required: '  .',
                                minLength: {
                                    value: 8,
                                    message: '8   .',
                                },
                                pattern : {
                                    value : /(?=.*[0-9])(?=.*[a-z])(?=.*\W)(?=\S+$).{8,20}/,
                                    message : ', ,   8~20'
                                },
                                validate: {
                                    check: (val) => {
                                        if (getValue('password') !== val) {
                                            return '  .';
                                        }
                                    },
                                },
                            })}
                        />
                        {errors.passwordConfirm && <small role='alert'>{errors.passwordConfirm.message}</small>}
                    </InputDiv>
                </Grid>
                <SubmitButton name='' width='15rem' height='3rem' color='#EB9332'/>
            </Form>
        </Container>
    );
}

export default SignUp;